<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/cetakburenanmwkecil" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = 8.5 x 13 in <br> *Orientasi = Portrait</p>
        <template id="template">
                <div class="grid-container">
                    <template v-for="i in karton"> 
                    <div v-for="j in i['Jumlah']" :key="j.id" class="grid-item">
                        <table>
                            <tr>
                                <td><i>Ingredients</i></td>
                            </tr>
                            <tr height="18px" style="vertical-align: top;">
                                <td><i>{{ i["Ingredients"] }}</i></td>
                            </tr>
                            <tr>
                                <td>Nama Brg : {{ i['Kode Barang'] }}</td>
                            </tr>
                            <tr>
                                <td>No Bets : {{ i['No Bets'] }}</td>
                            </tr>
                            <tr>
                                <td>Exp Date : {{ i['Expired Date'] }}</td>
                            </tr>
                            <tr>
                                <td>Netto {{ i['Isi'] }}ml</td>
                            </tr>
                            <tr>
                                <td><i>Diproduksi Oleh :</i></td>
                            </tr>
                            <tr>
                                <td><i>PT. Niagara Kosmetik</i></td>
                            </tr>
                            <tr>
                                <td><i>Solo - Indonesia</i></td>
                            </tr>
                        </table>
                    </div>
                    </template>
                </div>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                karton: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'cetakmwkecil/cetak'
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
   .grid-container{
            display: grid;
            grid-template-columns: repeat(10, 2cm);
            font-size: 4pt;
            font-family: "Times New Roman", Times, serif;
            text-align: center;
            margin-top: 15.01mm;
            margin-bottom: 15.01mm;
            margin-left: 8.99mm;
            margin-right: 8.01mm;
        }

        .grid-item{
            height: 2.504cm;
            width: 1.6cm;
            margin-right: 0.42cm;
            margin-bottom: 0.543cm;
        }
</style>